<script>
import {
    Form,
    Fieldset,
    Field,
    Span,
    Input,
    Button,
    DatePicker,
    Uploader,
    Select,
    Textarea,
    Checkbox,
    CheckboxGroup,
    RadioGroup,
    NumberInput
} from 'veui';
import moment from 'moment';
import bus from '../bus';
import 'vue-awesome/icons/indent';

export default {
    name: 'demo-form',

    components: {
        'veui-span': Span,
        'veui-input': Input,
        'veui-number-input': NumberInput,
        'veui-button': Button,
        'veui-form': Form,
        'veui-fieldset': Fieldset,
        'veui-field': Field,
        'veui-datepicker': DatePicker,
        'veui-uploader': Uploader,
        'veui-select': Select,
        'veui-checkbox': Checkbox,
        'veui-checkboxgroup': CheckboxGroup,
        'veui-radiogroup': RadioGroup,
        'veui-textarea': Textarea
    },

    data() {
        let hobby = [''];
        let hobbyItems = [
            {
                value: '',
                label: ''
            },
            {
                value: '',
                label: ''
            },
            {
                value: '',
                label: ''
            },
            {
                value: '',
                label: ''
            }
        ];
        let phoneType = 'mobile';
        let phoneTypeOptions = [
            {
                label: '',
                value: 'phone'
            },
            {
                label: '',
                value: 'mobile'
            }
        ];
        return {
            demoData: [
                {
                    label: '[ui=special]',
                    ui: 'special'
                },
                {
                    label: '[ui=few]',
                    ui: 'few'
                },
                {
                    label: '[ui=long]',
                    ui: 'long'
                }
            ],
            storeData1: {
                nickName: '',
                sex: '',
                married: false,
                marryItems: [
                    {
                        value: true,
                        label: ''
                    },
                    {
                        value: false,
                        label: ''
                    }
                ],
                sexItems: [
                    {
                        value: '-',
                        label: ''
                    },
                    {
                        value: '',
                        label: ''
                    },
                    {
                        value: '',
                        label: ''
                    }
                ],
                hobby,
                hobbyItems,
                birthday: new Date(),
                avatar: 'https://www.baidu.com/img/bd_logo1.png'
            },
            storeData2: {
                lastName: '',
                firstName: '',
                hobby,
                hobbyItems,
                phone: '18888888888',
                phoneType,
                phoneTypeOptions,
                start: null,
                end: null,
                salary: [2, 5]
            },
            storeData3: {
                statusSelected: 1,
                statusOptions: [
                    {
                        label: '1',
                        value: 1
                    },
                    {
                        label: '2',
                        value: 2
                    },
                    {
                        label: '3',
                        value: 3
                    },
                    {
                        label: '4',
                        value: 4
                    }
                ],
                range: [
                    moment().toDate(),
                    moment()
                        .add(3, 'month')
                        .toDate()
                ]
            },
            storeData4: {
                name: 'liyunteng1',
                name1: 'liyunteng2',
                age: null,
                desc: '',
                hobby,
                phone: '18888888888',
                phoneType,
                start: null,
                end: null,
                protocol: '',
                floor: 3500
            },
            storeData4Options: {
                hobbyItems,
                phoneTypeOptions
            },
            requiredRule: [
                {
                    name: 'required',
                    value: true,
                    triggers: 'blur,input'
                }
            ],
            numRequiredRule: [
                {
                    name: 'numeric',
                    value: true,
                    triggers: 'blur,input'
                },
                {
                    name: 'required',
                    value: true,
                    triggers: 'blur,input'
                }
            ],
            protocolRequiredRule: [
                {
                    name: 'required',
                    value: true,
                    message: '',
                    triggers: 'change'
                }
            ],
            dynamicNameRule: [
                {
                    name: 'required',
                    value: true,
                    triggers: 'blur,input'
                },
                {
                    name: 'minLength',
                    value: 2
                }
            ],
            ageRule: [
                {
                    name: 'required',
                    value: true,
                    triggers: 'input'
                },
                {
                    name: 'numeric',
                    value: true,
                    triggers: 'input'
                },
                {
                    name: 'maxLength',
                    value: 3,
                    triggers: 'change'
                }
            ],
            hobbyRule: [
                {
                    name: 'minLength',
                    value: 3,
                    message: '',
                    triggers: 'change'
                }
            ],
            isValidating: false,
            validators: [
                {
                    fields: ['start', 'end'],
                    handler(start, end) {
                        if (start == null || end == null) {
                            return true;
                        }

                        if (parseInt(start, 10) >= parseInt(end, 10)) {
                            return {
                                start: ''
                            };
                        }
                        return true;
                    },
                    triggers: ['change', 'submit,input']
                },
                {
                    fields: ['phone'],
                    validate(phone) {
                        return new Promise(function (resolve) {
                            setTimeout(function () {
                                let res;
                                if (phone === '18888888888') {
                                    res = {
                                        phone: ''
                                    };
                                }
                                return resolve(res);
                            }, 3000);
                        });
                    },
                    triggers: ['input']
                }
            ],
            beforeValidate() {
                bus.$emit('log', 'beforeValidate');
                this.isValidating = true;
            },
            afterValidate() {
                bus.$emit('log', 'afterValidate');
                this.isValidating = false;
            },
            storeData5: {
                qindian: 'Evan You',
                scheduleInfo: [
                    {
                        project: 'vuejs',
                        range: [
                            moment().toDate(),
                            moment()
                                .add(3, 'month')
                                .toDate()
                        ]
                    }
                ]
            },
            qindianValidator: [
                {
                    // 
                    //  submit 
                    fields: ['qindian', 'qindian'],
                    handler(qindian) {
                        if (qindian !== 'Evan You') {
                            return {
                                qindian: ''
                            };
                        }
                        return true;
                    },
                    triggers: ['input', 'submit']
                }
            ]
        };
    },

    methods: {
        handleInvalid(e) {
            bus.$emit('log', 'handleInvalid', e);
            this.isValidating = false;
        },
        submit(data, e) {
            bus.$emit('log', 'submit', data, e);
        },
        dynamicAdd() {
            this.storeData5.scheduleInfo.push({
                project: null,
                range: null
            });
        },
        dynamicDelete(index) {
            this.storeData5.scheduleInfo.splice(index, 1);
        }
    }
};
</script>

