    <article>
        <h1><code>&lt;veui-textarea&gt;</code></h1>
        <veui-form>
            <h2></h2>
            <div class="options-desc">
                 <span class="bg-gray-show">ui</span> 
                 <span class="bg-gray-show">s / m</span>
            </div>
            <section>
                <veui-field
                    ui="s"
                    label="s"
                >
                    <veui-textarea
                        v-model="value"
                        autofocus
                        ui="s"
                        rows="3"
                    />
                </veui-field>
                <veui-field
                    ui="s"
                    label="m"
                >
                    <veui-textarea
                        v-model="value"
                        autofocus
                        ui="m"
                        rows="3"
                        resizable
                    />
                </veui-field>
            </section>
            <section>
                <h3></h3>
                <veui-textarea
                    placeholder=""
                    v-model="value"
                    readonly
                />
            </section>
            <section>
                <h3></h3>
                <veui-textarea
                    placeholder=""
                    v-model="value"
                    disabled
                />
            </section>
            <section>
                <h3></h3>
                <veui-textarea
                    placeholder=""
                    v-model="value"
                    invalid
                />
            </section>

            <section>
                <h3>()</h3>
                <section>
                    <veui-textarea
