<script>
import bus from '../bus';
import {Textarea, Form, Field} from 'veui';

export default {
    name: 'button-demo',
    components: {
        'veui-textarea': Textarea,
        'veui-field': Field,
        'veui-form': Form
    },
    data() {
        return {
            value: ''
        };
    },
    mounted() {
        this.$children.forEach(child => {
            child.$on('click', () => {
                bus.$emit('log', child.$el.getAttribute('ui'));
            });
        });
    }
};
</script>

