<template>
    <article>
        <h1>
            <code>&lt;veui-collapse&gt;</code> &amp;
            <code>&lt;veui-accordion&gt;</code>
        </h1>
        <h2></h2>
        <div class="options-desc"> <span class="bg-gray-show">ui</span>  <span class="bg-gray-show">s() / m</span></div>
        <h2></h2>
        <section>
            <div class="title-desc">[ui=s]</div>
            <veui-collapse label="">
                Content
            </veui-collapse>
        </section>
        <section>
            <div class="title-desc">[ui=m]</div>
            <veui-collapse label="" ui="m">
                Content
            </veui-collapse>
        </section>
        <section>
            <div class="title-desc">.sync</div>
            <veui-checkbox v-model="expanded"></veui-checkbox>
            <veui-collapse
                :expanded.sync="expanded"
                label=""
            >
                Content
            </veui-collapse>
        </section>

        <section>
            <div class="title-desc"></div>
            <veui-checkbox v-model="expanded2"></veui-checkbox>
            <veui-collapse
                :expanded="expanded2"
                label=""
                @toggle="toggle2"
            >
                Content
            </veui-collapse>
        </section>

        <section>
            <div class="title-desc"></div>
            <veui-collapse
                disabled
                label=""
            >
                Content
            </veui-collapse>
        </section>

        <h2></h2>
        <section>
            <div class="title-desc">[ui=s]</div>
            <veui-accordion>
                <veui-collapse label="">
                    Content
                </veui-collapse>
                <veui-collapse label="">
                    Content
                </veui-collapse>
                <veui-collapse
                    label=""
                    disabled
                >
                    Content
                </veui-collapse>
            </veui-accordion>
        </section>

        <section>
            <div class="title-desc">[ui=m]</div>
            <veui-accordion ui="m">
                <veui-collapse label="" ui="m">
                    Content
                </veui-collapse>
                <veui-collapse label="" ui="m">
                    Content
                </veui-collapse>
                <veui-collapse
                    label=""
                    disabled
                    ui="m"
                >
                    Content
                </veui-collapse>
            </veui-accordion>
        </section>

        <section>
            <div class="title-desc">.sync & multiple</div>
            <veui-checkbox-group
                v-model="expanded3"
                :items="items"
            />
            <veui-accordion
                multiple
                :expanded.sync="expanded3"
            >
                <veui-collapse
                    label=""
                    name="1"
                >
                    Content
                </veui-collapse>
                <veui-collapse
                    label=""
                    name="2"
                >
                    Content
                </veui-collapse>
                <veui-collapse
                    label=""
                    name="3"
                >
                    Content
                </veui-collapse>
            </veui-accordion>
            <p>{{ expanded3 }}</p>
        </section>

        <section>
            <div class="title-desc"></div>
            <veui-accordion
                :expanded="expanded4"
                @toggle="toggle4"
            >
                <veui-collapse
                    label=""
                    name="1"
                >
                    Content
                </veui-collapse>
                <veui-collapse
                    label=""
                    name="2"
                >
                    Content
                </veui-collapse>
                <veui-collapse
                    label=""
                    name="3"
                >
                    Content
                </veui-collapse>
            </veui-accordion>
            <p>{{ expanded4 }}</p>
        </section>

        <section>
            <div class="title-desc"></div>
            <veui-accordion>
                <veui-collapse label="">
                    <veui-accordion>
                        <veui-collapse label="">
                            <veui-accordion>
                                <veui-collapse label="">
                                    Content
                                </veui-collapse>
                                <veui-collapse label="">
                                    Content
                                </veui-collapse>
                                <veui-collapse label="">
                                    Content
                                </veui-collapse>
                            </veui-accordion>
                        </veui-collapse>
                        <veui-collapse label="">
                            Content
                        </veui-collapse>
                        <veui-collapse label="">
                            Content
                        </veui-collapse>
                    </veui-accordion>
                </veui-collapse>
                <veui-collapse
                    label=""
                    disabled
                >
                    Content
                </veui-collapse>
                <veui-collapse label="">
                    Content
                </veui-collapse>
            </veui-accordion>
        </section>
    </article>
</template>

<script>
import Vue from 'vue';
import {Checkbox, CheckboxGroup, Collapse, Accordion} from 'veui';
import confirm from 'veui/plugins/confirm';

Vue.use(confirm);

export default {
    name: 'collapse-demo',
    components: {
        'veui-checkbox': Checkbox,
        'veui-checkbox-group': CheckboxGroup,
        'veui-collapse': Collapse,
        'veui-accordion': Accordion
    },
    data() {
        return {
            expanded: false,
            expanded2: false,
            expanded3: [],
            expanded4: '3',
            items: [
                {label: '', value: '1'},
                {label: '', value: '2'},
                {label: '', value: '3'}
            ]
        };
    },
    methods: {
        toggle2(val) {
            if (!val) {
                this.expanded2 = val;
                return;
            }
            this.$confirm('').then(confirmed => {
                if (confirmed) {
                    this.expanded2 = val;
                }
            });
        },
        toggle4(val, name, expanded) {
            if (!val) {
                this.expanded4 = expanded;
                return;
            }
            this.$confirm('').then(confirmed => {
                if (confirmed) {
                    this.expanded4 = expanded;
                }
            });
        }
    }
};
</script>

<style lang="less" scoped>
section {
  margin-bottom: 10px;
}
