<template>
    <article>
        <h1><code>&lt;veui-pagination&gt;</code></h1>
        <h2></h2>
        <div class="options-desc">
            <span class="bg-gray-show">ui</span>
            
            <span class="bg-gray-show">xs / s / m</span>
        </div>
        <h2></h2>
        <div class="options-desc">
            <span class="bg-gray-show">ui</span>
            
            <span class="bg-gray-show">normal / </span>
        </div>
        <section>
            <h3>[ui=normal]</h3>
            <section>
                <span class="pagination-type">[ui=xs]</span>
                <veui-pagination
                    :page="page"
                    :total="total"
                    :page-sizes="pageSizes"
                    :to="to"
                    ui="xs normal"
                    :page-size.sync="pageSize"
                />
            </section>

            <section>
                <span class="pagination-type">[ui=s]</span>
                <veui-pagination
                    :page="page"
                    :total="total"
                    :to="to"
                    ui="normal"
                />
            </section>

            <section>
                <span class="pagination-type">[ui=m]</span>
                <veui-pagination
                    :page="page"
                    :total="total"
                    ui="m normal"
                    :to="to"
                />
            </section>
        </section>

        <section>
            <h3></h3>
            <section>
                <span class="pagination-type">[ui=xs]</span>
                <veui-pagination
                    :page="page"
                    :total="total"
                    :page-sizes="pageSizes"
                    :to="to"
                    goto
                    ui="xs"
                    :page-size.sync="pageSize"
                />
            </section>

            <section>
                <span class="pagination-type">[ui=s]</span>
                <veui-pagination
                    :page="page"
                    :total="total"
                    :to="to"
                    goto
                />
            </section>

            <section>
                <span class="pagination-type">[ui=m]</span>
                <veui-pagination
                    ui="m"
                    :page="page"
                    :total="total"
                    :to="to"
                    goto
                />
            </section>
        </section>

        <section>
            <h3></h3>
            <section>
                <small> &lt;router-link&gt;  to prop </small>
            </section>
            <section>
                <veui-pagination
                    :page="page"
                    :total="total"
                    :to="{ name: 'Pagination', params: { page: ':page' } }"
                />
            </section>
        </section>

        <section>
            <h3></h3>
            <section>
                <veui-pagination
                    :page="page"
                    :total="total"
                    :to="to"
                    native
                />
            </section>
        </section>

        <section>
            <h3></h3>
            <section>
                <small> <code>to</code> </small>
            </section>
            <section>
                <veui-pagination
                    :page="page"
                    :total="total"
                    :to="to"
                    native
                    @redirect="handlePageRedirect"
                />
            </section>
            <div class="message">
                {{ fifthPaginationMessage }}
            </div>
        </section>

        <section>
            <h3></h3>
            <section>
                <veui-pagination
                    :page="p"
                    :total="total"
                    @redirect="handleCustomRedirect"
                />
            </section>
        </section>

        <section>
            <h3></h3>
            <section>
                <veui-pagination
                    :page="1"
                    :total="0"
                    :to="to"
                    native
                />
            </section>
        </section>
    </article>
</template>

<script>
import bus from '../bus';
import {Pagination} from 'veui';

export default {
    name: 'pagination-demo',
    components: {
        'veui-pagination': Pagination
    },
    data() {
        return {
            page: parseInt(this.$route.params.page, 10) || 1,
            total: 10101,
            to: '/pagination/:page',
            pageSize: 30,
            pageSizes: [30, 60, 100, 200],
            fifthPaginationMessage: '',
            p: 1
        };
    },
    mounted() {
        this.$children.forEach(child => {
            child.$on('click', () => {
                bus.$emit('log', child.$el.getAttribute('ui'));
            });
        });
    },
    methods: {
        handlePageRedirect(page, event) {
            event.preventDefault();
            this.fifthPaginationMessage = `${page}`;
        },
        handleCustomRedirect(page) {
            this.p = page;
        }
    },
    beforeRouteUpdate({params}, from, next) {
        let page = parseInt(params.page, 10);
        this.page = isNaN(page) ? 1 : page;
        next();
    }
};
</script>

<style lang="less" scoped>
@import (reference) url('~@/variables/index.less');
section {
    margin-bottom: 3em;

    section {
        margin-bottom: 2em;
        display: flex;
        .veui-pagination {
            flex: 1;
        }
        .pagination-type {
            color: @veui-font-color-weak;
            font-size: @veui-font-size-s;
            margin-right: 23px;
        }
    }
}
</style>
